div.OsAppGroupSmall {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 25%;
    margin: 3.125% 3.125%;
    width: 25%;
}

/* set right margin on the last item of each row */
div.OsAppGroupSmall:nth-child(3n) {
    margin-right: 6.25%;
}

/* set left margin on the first item of each row */
div.OsAppGroupSmall:nth-child(3n - 2) {
    margin-left: 6.25%;
}

/* set top margin on the first row of items */
div.OsAppGroupSmall:nth-child(-n + 3) {
    margin-top: 6.25%;
}

/* set bottom margin on the last row of items */
div.OsAppGroupSmall:nth-child(n+7):nth-child(-n+9) {
    margin-bottom: 6.25%;
}

div.OsAppGroupSmallBackground {
    border-radius: 0.4rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 100%;
    width: 100%;
}

div.OsAppGroupSmallContainer {
    -ms-flex-line-pack: start;
        align-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    background-size: auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
}



