div.desktop__container {
    background: -webkit-gradient(linear, left top, left bottom, from(#2c73ba), to(#5A7B9B));
    background: -webkit-linear-gradient(#2c73ba, #5A7B9B);
    background: -o-linear-gradient(#2c73ba, #5A7B9B);
    background: linear-gradient(#2c73ba, #5A7B9B); 
    height: 100vh;
    padding-top: 1rem;
}

div.appgroup__container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    height: 55%;
    padding: 1rem;
    margin-top: 1rem;
}

div.os__image {
    margin: 0 auto;
    width: 50%;
}

div.os__image img {
    display: block;
    margin: 0 auto;
}



